    constructor(
        private http: Http,
        private router: Router,
        private reportService: ReportService, private modalService: NgbModal, private datePipe: DatePipe, public dialog: MatDialog, private route: ActivatedRoute,

        private masterReadService: MasterReadService
    ) {
        if ( sessionStorage.length == 0 ) {
            this.isLoggedIn = false;
            swal( {
                title: "Session Expired",
                text: "Please relogin to access the application!",
                icon: "error",
                closeOnClickOutside: false,
                closeOnEsc: false,
            } ).then(() => {
                this.logInPage();
            } )
        }
        if ( this.isLoggedIn ) {
            this.userDataDtoReturnSession = JSON.parse( sessionStorage.getItem( 'SRDWeb' ) );
            //for modal starts
            this.routeQueryParams$ = route.queryParams.subscribe( params => {
                if ( params['dialog'] ) {
                    this.shortExtraDialog();
                }
            } );
            this.routeQueryParams$ = route.queryParams.subscribe( params => {
                if ( params['dialog'] ) {
                    this.multipelArticleSizeDialog();
                }
            } );

            //for modal ends

            ///Unloading Charge for chennai
            if ( this.viewUnloadingChg == "Show Unloading Charge for Memo In Lr Enquiry" ) {
                this.showUnloadingChg = true;
            } else {
                this.showUnloadingChg = false;
            }
            if ( this.showLrPrintLbl = "Show LR Copy In LR Enquiry" ) {
                this.lblPrint = true;
            }
            if ( this.showConsigneeBusLbl = "Show Consignee Business view In Lr Enquiry" ) {
                this.lblConsigneeBusinessDetails = true;
            }
        }

    }
